import React, { useState } from 'react';
import { Search as SearchIcon, Bell } from 'lucide-react';

const Search = () => {
  const [searchQuery, setSearchQuery] = useState('');

  const handleKeyDown = (e) => {
    if (e.key === 'Enter') {
      console.log('Search triggered for:', searchQuery);
    }
  };

  return (
    <div className="w-full p-2 bg-[#f3f3f3]">
      <div className="flex justify-between items-center">

        {/* Left Title */}
        <h1 className="text-2xl font-bold text-gray-800">Analytics</h1>

        {/* Right Search + Bell */}
        <div className="flex items-center gap-4">

          {/* Search Bar */}
          <div className="flex items-center gap-2 bg-white rounded-full px-5 py-2 shadow-sm border w-[380px]">
            <SearchIcon className="w-5 h-5 text-gray-600" />

            <input
              type="text"
              placeholder="Search By User ID"
              className="outline-none w-full text-gray-700"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              onKeyDown={handleKeyDown}
            />
          </div>

          {/* Bell Icon */}
          <div className="relative">
            <Bell className="h-6 w-6 text-yellow-500" />
            <span className="absolute -top-1 -right-1 h-2.5 w-2.5 bg-red-500 rounded-full border-2 border-white" />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Search;
